.certifications-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    text-align: center;
    min-height: 100vh;
    padding-bottom: 200px;
  }
  
  .certifications-page__title {
    margin-bottom: rem;
  }
  
  .certifications-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 3rem;
  }
  
  .certifications-page__title {
    margin-bottom: 2rem;
    text-align: center;
    padding-top: 35px;
  }
  
  .certifications-page__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 6rem;
    max-width: 100%;
    padding-top: 40px;
  }
  
  .certifications-page__group {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    max-width: 100%;
    position: relative;
  }
  
  .certifications-page__group:first-child:before {
    top: 50%;
  }
  
  .certifications-page__group:last-child:before {
    bottom: 50%;
  }
  
  .certifications-page__group-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 3rem;
    max-width: 100%;
  }
  
  .certifications-page__group-item img {
    max-width: 100%;
    height: auto;
    max-height: 500px;
  }

  .certifications-page__group-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    max-width: 100%;
    text-align: center;
  }
  
  .certifications-page__group-item .certificate-link {
    display: inline-block;
    padding: 0.5rem 1rem;
    border: 1px solid #000;
    border-radius: 5px;
  }
  
  .certifications-page__group-item .certificate-link:hover {
    background-color: #000;
    color: #fff;
  }
  
  .certifications-page__group-item .certificate-link .live-icon {
    margin-right: 0.3rem;
    font-size: 1rem;
  }

  .live-icon {
    margin-right: 5px;
  }
  
  